.navbar {
  background-color: var(--primarycolor);
  padding: 1px 0;
  position: sticky;
  top: 0;
  z-index: 999;
  display: flex;
  justify-content: center;
}
.brand {
  display: none;
}
.navbar ul {
  display: flex;
  list-style: none;
  justify-content: left;
  cursor: pointer;
}
.navbar ul li {
  margin: 10px 50px;
  padding: 10px 14px 15px;
  transition: 0.2s ease;
  border-bottom: 2px solid transparent;
}
.navbar ul li a {
  text-decoration: none;
  color: black;
}
.navbar li:hover {
  border-bottom: 2px solid var(--tertiarycolor);
  transition: 0.2s ease;
}
.ham {
  display: none;
}

.ham .middle {
  background-color: black;
  height: 2px;
  transition: 0.3s ease-in;
}

.ham .top {
  background-color: black;
  height: 2px;
  margin: 2px 0 8px 0;
  width: 25px;
  transition: 0.3s ease-in;
}
.ham .bottom {
  background-color: black;
  height: 2px;
  margin: 8px 0 2px 0;
  width: 19px;
  transition: 0.3s ease-in;
}
.ham-menu {
  position: fixed;
  z-index: 999;
  right: 1455px;
  height: 100vh;
  width: 100vw;
  background-color: #fff;
  transition: 0.3s ease-in;
  text-align: center;
}
.ham-menu ul li {
  list-style: none;
  margin: 30px 0;
  font-size: 1.2rem;
}
.ham-menu a {
  text-decoration: none;
  color: #000;
}
.sideBar-nav {
  margin-right: 20px;
  display: none;
}
.sideBar-nav h2 {
  font-size: 1.6rem;
}

@media screen and (max-width: 680px) {
  .navbar ul {
    display: none;
  }
  .brand {
    display: block;
    margin-right: 20px;
  }
  .navbar {
    margin-top: 0;
    justify-content: space-between;
    align-items: center;
    padding: 5px 0;
  }
  .ham {
    display: block;
    width: 30px;
    height: 30px;
    margin-left: 20px;
  }
  .sideBar-nav {
    margin-right: 20px;
    display: block;
    transition: all 0.3s ease;
  }
  .sideBar-nav-open {
    transform: rotateY(180deg);
    transition: all 0.4s ease;
  }
  .ham-menu ul {
    padding: 0;
  }
  .ham-open .top {
    transform: translatey(9px) rotate(45deg);
    transition: 0.3s ease-in-out;
    width: 30px;
  }
  .ham-open .middle {
    transform: translatex(-30px);
    background-color: transparent;
    transition: 0.3s ease-in;
  }
  .ham-open .bottom {
    transform: translateY(-10.5px) rotate(-45deg);
    transition: 0.3s ease-in-out;
    width: 30px;
  }
  .ham-menu-open {
    right: 0;
    transition: 0.2s ease;
  }
}
