.tranding {
  flex: 3;
  margin: 20px 90px;
  overflow: hidden;
}
.tranding .tag-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.head-tag h3 {
  font-size: 2rem;
  font-weight: 500;
}
.trand-navig {
  display: flex;
  align-items: center;
}
.trand-navig div {
  background-color: var(--primarycolor);
  margin: 0 10px;
  padding: 5px 5px;
  border-radius: 10px;
}
.trand-navig div i {
  margin: 0 15px;
  font-size: 1rem;
}
.tr-sing {
  display: flex;
  margin-top: 20px;
  overflow-x: scroll;
}
.tranding-posts {
  display: flex;
  cursor: grab;
  scroll-behavior: smooth;
}

.tranding-single-post {
  width: 250px;
  height: 300px;
  margin: 10px 20px;
  position: relative;
}
.tranding-single-post img {
  width: 100%;
  height: 100%;
  opacity: 0.9;
  background-color: black;
}

.tranding-single-post div {
  position: absolute;
  bottom: 0;
  padding-left: 10px;
  background-color: rgba(27, 27, 27, 0.671);
}
.tranding-single-post a {
  font-size: 0.9rem;
  text-decoration: none;
  color: #fff;
  padding-top: 8px;
  font-weight: 200;
}
.tranding-single-post p {
  font-weight: 200;
  color: white;
}
.tranding-single-post a:hover {
  text-decoration: underline;
}
@media screen and (max-width: 680px) {
  .tranding {
    flex: 1;
    margin: 20px 5px;
    overflow: hidden;
  }
  .head-tag h3 {
    font-size: 1rem;
  }
  .trand-navig div i {
    font-size: 0.9rem;
  }
  .tranding-single-post {
    width: 150px;
    height: 180px;
    margin: 5px 10px;
  }
  .tranding-single-post a {
    font-size: 0.7rem;
    color: #fff;
    font-weight: 300;
    padding-top: 8px;
    line-height: -5px;
  }
  .tranding-single-post p {
    display: none;
  }
  .trand-navig div {
    background-color: var(--primarycolor);
    margin: 0 7px;
    padding: 3px 3px;
  }
}
