.header {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 10px 0;
}
.brandname {
  flex: 1;
  text-align: center;
}
.brandname h1 {
  font-family: var(--brndfont);
  font-size: 3rem;
}
.Search {
  flex: 1;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.khoj input {
  outline: none;
  border: none;
  background-color: var(--primarycolor);
  padding: 7px 12px;
  margin-right: 10px;
  font-size: 15px;
  border-radius: 25px;
}
.switch {
  font-size: 17px;
  position: relative;
  display: inline-block;
  width: 3em;
  height: 1.5em;
}
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
  border-radius: 30px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 1.1em;
  width: 1.1em;
  border-radius: 20px;
  left: 0.3em;
  bottom: 0.2em;
  background-color: white;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: rgb(90, 226, 142);
}

input:focus + .slider {
  box-shadow: 0 0 1px rgb(101, 238, 149);
}

input:checked + .slider:before {
  transform: translateX(1.2em);
}

/* Dark Theme */
.dark {
  background-color: #121212;
}
.dark .brandname {
  color: #fff;
}
.dark .Search i{
  color: #fff;
}
@media screen and (max-width: 580px) {
  .header {
    display: none;
    margin-top: 0;
  }
}
