.sidebar {
  width: 500px;
  height: 100vh;
  flex: 1;
  position: sticky;
  top: 80px;
  margin-right: 20px;
  padding: 0 10px;
}

.sideslider {
  overflow-y: scroll;
  height: 100vh;
}
.tag-head {
  border-bottom: 1px solid var(--secondrycolor);
  margin-right: 20px;
  padding-bottom: 8px;
}
.tag-head h3 {
  font-family: var(--headingfont);
  font-size: 1.4rem;
}
.tag-items ul {
  padding: 5px;
}

.tag-items li {
  background-color: var(--primarycolor);
  list-style: none;
  display: inline-block;
  padding: 5px 9px;
  margin: 5px 9px;
  font-family: var(--subheadingfont);
  font-size: 1rem;
  cursor: pointer;
}
.tag-items li:hover {
  background-color: var(--tertiarycolor);
  transition: 0.3s ease-in;
}

.pppst {
  margin-top: 1.2rem;
  margin-bottom: 1.2rem;
}
.pp-posts {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
  cursor: pointer;
}

.post-img img {
  width: 120px;
  height: 80px;
  margin-right: 13px;
}
.post-details {
  flex: 1;
  margin-right: 5px;
}
.post-details h3 {
  font-size: 0.9rem;
}
.post-details p {
  font-size: 0.7rem;
  margin-top: 8px;
}

@media screen and (max-width: 680px) {
  .sidebar {
    left: 500px;
    display: none;
    transition: 0.3s ease;
    width: 500px;
  }
  .sidebar-open .sideslider {
    width: 90vw;
  }
  .sidebar-open .populer {
   padding-bottom: 50px;
  }
  .sidebar-open {
    position: sticky;
    display: block;
    transition: 0.3s ease;
    left: 0;
  }
}
