.home {
  display: flex;
  margin: 0 10px 10px 90px;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

.home .left {
  flex: 3;
  margin-right: 22px;
}
.home-post {
  display: flex;
  margin: 50px 5px;
}
.home-postimg img {
  width: 300px;
  height: 220px;
  margin-right: 17px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.home-postdetails h2 {
  font-size: 1.4rem;
  margin-bottom: 12px;
}
.home-postdetails a {
  text-decoration: none;
  color: var(--secondrycolor);
  margin-bottom: 12px;
}

.home-postdetails a:hover {
  color: var(--tertiarycolor);
  text-decoration: underline;
}
.home-postdetails p {
  margin-top: 12px;
  color: rgb(95, 95, 95);
}

@media screen and (max-width: 680px) {
  .home {
    margin: 0;
    justify-content: center;
  }
  .home .left {
    margin-right: 0;
    flex: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .home-post {
    flex: auto;
    flex-direction: column;
    margin: 10px 10px;
    justify-content: center;
    width: 90vw;
  }
  .home-postimg img {
    width: 90vw;
    height: 30vh;
    margin: 0;
    margin-bottom: 15px;
  }
  .home-postdetails h2 {
    font-size: 1.3rem;
    margin-bottom: 12px;
    margin-bottom: 0;
  }
  .home-postdetails .date {
    display: none;
  }
  .home-postdetails p {
    margin-top: 10px;
    font-size: 0.8rem;
  }
  .home-postdetails{
    margin-left: 5px;
  }
}
